header {
  position: sticky;
  width: 100%;
  top: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  z-index: 10;
  background-color: #0d171d;
  
}

.header__search-box {
  width: 300px;
  height: 345px;
  overflow-y: scroll;
  position: relative;
  position: absolute;
  padding: 10px;
  top: 100%;
 border-radius: 12px;
 border: 1px solid #8c8c8c;
 background-color: #0d171d;
  color: #a1d737;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
  text-align: center;
display: none;
  visibility: hidden;
  @media screen and (min-width: 768px) {
     left: 75%;
  }
}

.header__close-icon{
  position: absolute;
  left: 75.3%;
  top: 103%;
  z-index: 12;
  display: none;
}

.header__error-img{
  display: none;
  visibility: hidden;
  width: 282px;
}

.header__error-text{
  display: none;
  visibility: hidden;
}


.header__container {
  display: flex;
  justify-content: space-between;
}

.header__links{
  display: flex;
  align-items: center;
  gap: 10px;
}

.header__link-characters{
  color: #0d171d;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 16px;
  /* 133.333% */
  display: flex;
  padding: 10px 20px;
  align-items: flex-start;
  gap: 10px;
  border-radius: 1000px;
  background: #a1d737;
    border: 1px solid #a1d737;
      transition-duration: 250ms;

  @media screen and (min-width: 768px) {
    width: 73px;
    display: flex;
    padding: 12px 24px;
    align-items: flex-start;
    gap: 10px;
    border-radius: 1000px;
    background: #a1d737;
    color: #0d171d;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 18px;
    /* 128.571% */
    margin-right: 1px;
  }
}

.header__link-characters:hover {
  background: #0d171d;
  color:#a1d737;
    transition-duration: 250ms;
}

.header__link-episodes {
  display: flex;
  padding: 10px 20px;
  align-items: flex-start;
  gap: 10px;
  border-radius: 1000px;
  border: 1px solid rgba(250, 250, 250, 0.3);
  color: #fafafa;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 16px;
  justify-content: center;
    transition-duration: 250ms;

  /* 133.333% */
  @media screen and (min-width: 768px) {
    width: 73px;
    display: flex;
    padding: 12px 24px;
    align-items: flex-start;
    gap: 10px;
    border-radius: 1000px;
    border: 1px solid rgba(250, 250, 250, 0.3);
    color: #fafafa;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 18px;
    /* 128.571% */
  }
}

.header__link-episodes:hover {
  background: #fafafa;
  color: #0d171d;
    transition-duration: 250ms;
}

.header__logo {
  height: 48px;
flex-shrink: 0;
}

.header__search {
  width: 73px;
   height: 39px;
  padding: 0px 24px;
  border-radius: 1000px;
  border: 1px solid rgba(250, 250, 250, 0.3);
  color: #fafafa;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 16px;
  /* 133.333% */
  background-color: transparent;
  text-align:right;

  @media screen and (min-width: 768px) {
    display: block;
    font-size: 14px;
    line-height: 18px;
    display: flex;
    justify-content: flex-end;
    gap: 8px;
    border-radius: 1000px;
    border: 1px solid rgba(250, 250, 250, 0.3);
  }
}

.header__search-btn{
  background: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__form {
  margin-left: auto;
  position: relative;
  display: flex;
}

.header__search-icon {
  position: absolute;
  top: 13px;
  left: 24px;
  width: 14px;
  height: 14px;
  flex-shrink: 0;
}

.scroll-down {
  position: fixed;
  z-index: 10;
  bottom: 24px;
  right: 24px;
  width: 48px;
  height: 48px;
  background-color: transparent;
  border-radius: 50%;
  border: #8c8c8c 1px solid;
  display: flex;
  justify-content: center;
  align-items: center;
    transition-duration: 250ms;
}

.scroll-down-svg {
  width: 36x;
  height: 36px;
 stroke: #FAFAFA;
}

.scroll-down:hover {
  border-color: #a1d737;
    transition-duration: 250ms;
}

.scroll-down:hover svg {
  stroke: #a1d737;
    transition-duration: 250ms;
}