.container {
  width: 100%;
  max-width: 343px;
  margin: 0 auto;

  @media screen and (width >= 768px) {
    max-width: 704px;
    padding-left: 10px;
    padding-right: 10px;
  }

  @media screen and (width >= 1200px) {
    max-width: 1184px;
  }
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  background-color: #fafafa;
  font-family: Montserrat;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  box-sizing: border-box;
}

button {
  cursor: pointer;
  padding: 0;
}

header {
  z-index: 10;
  background-color: #0d171d;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  position: sticky;
  top: 0;
}

.header__search-box {
  color: #a1d737;
  text-align: center;
  visibility: hidden;
  background-color: #0d171d;
  border: 1px solid #8c8c8c;
  border-radius: 12px;
  width: 300px;
  height: 345px;
  padding: 10px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
  display: none;
  position: absolute;
  top: 100%;
  overflow-y: scroll;

  @media screen and (width >= 768px) {
    left: 75%;
  }
}

.header__close-icon {
  z-index: 12;
  display: none;
  position: absolute;
  top: 103%;
  left: 75.3%;
}

.header__error-img {
  visibility: hidden;
  width: 282px;
  display: none;
}

.header__error-text {
  visibility: hidden;
  display: none;
}

.header__container {
  justify-content: space-between;
