  padding: 10px 20px;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 16px;
  transition-duration: .25s;
  display: flex;

  @media screen and (width >= 768px) {
    color: #0d171d;
    background: #a1d737;
