.scroll-down {
  z-index: 10;
  background-color: #0000;
  border: 1px solid #8c8c8c;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  transition-duration: .25s;
  display: flex;
  position: fixed;
  bottom: 24px;
  right: 24px;
}

.scroll-down-svg {
  width: 36x;
  stroke: #fafafa;
  height: 36px;
}

.scroll-down:hover {
  border-color: #a1d737;
  transition-duration: .25s;
}

.scroll-down:hover svg {
  stroke: #a1d737;
  transition-duration: .25s;
}

